import uniRate from '../../components/uni-rate/uni-rate.vue';
export default {
	name: 'index',
	components: {
		uniRate
	},
	data() {
		return {
			banners: [],
			movieList: [], //电影
			trailer: [], // 预告
			likes: [] // 猜你喜欢
		};
	},
	onLoad() {
		this.getData();
		this.getMovieList();
		this.getTrailer();
		this.getLikes();
	},
	methods: {
		getData() {
			uni.request({
				url: `${this.$api}/carousel/list`,
				method: 'POST',
				data: {
					qq: '7929290'
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					if (res.data.status === 200) {
						this.banners = res.data.data;
					}
				}
			});
		},
		getMovieList() {
			uni.request({
				url: `${this.$api}/movie/hot`,
				method: 'POST',
				data: {
					qq: '7929290',
					type: 'superhero'
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					this.movieList = res.data.data;
				}
			});
		},
		getTrailer() {
			uni.request({
				url: `${this.$api}/movie/hot`,
				method: 'POST',
				data: {
					qq: '7929290',
					type: 'trailer'
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					this.trailer = res.data.data;
				}
			});
		},
		getLikes() {
			uni.request({
				url: `${this.$api}/guessULike`,
				method: 'POST',
				data: {
					qq: '7929290',
					type: 'trailer'
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					console.log(res.data.data);
					this.likes = res.data.data;
					this.likes.map(item => {
						item.createTime = this.$moment(item.createTime).format('YYYY-MM-DD');
					});
				}
			});
		}
	}
};
